import { Box, Stack } from "@mui/material";

import BaseText from "../components/text/BaseText";
import ScreenTitle from "../components/text/ScreenTitle";
import MyHead from "./MyHead";
import { myClasses } from "../classes/style";
import RefreshBtn from "../components/nav/RefreshBtn";
import DayCard from "../components/displays/DayCard";
import PayeeCard from "../components/displays/PayeeCard";
import PayerCard from "../components/displays/PayerCard";
import Button from "../components/inputs/Button";
import InvoicePrinter from "./InvoicePrinter";
import lsCache from "../classes/cache";
import { v4 } from "uuid";

export default function InvoiceConfirm(props) {
  document.title = `Confirm Invoice - ${MyHead.titleBase}`;
  const goToPrintScreen = () => {
    props.setPage(
      <InvoicePrinter
        setPage={props.setPage}
        invoiceDays={props.invoiceDays}
        period={props.period}
        payee={props.payee}
        payer={props.payer}
      />
    );
    // ? send the data to invoices ls
    lsCache.arrayAdd("invoices", {
      uuid: v4(),
      invoiceDays: props.invoiceDays,
      period: props.period,
      payee: props.payee,
      payer: props.payer,
    });
    props.invoiceDays.forEach((d) => {
      lsCache.arrayAdd("claimedDays", d);
      lsCache.arrayDel("ucDays", d.uuid);
    });
  };
  return (
    <div>
      <Box sx={{ ...myClasses.flexRow }}>
        <ScreenTitle>InvoiceConfirm</ScreenTitle>
        <RefreshBtn
          setPage={props.setPage}
          comp={
            <InvoiceConfirm
              setPage={props.setPage}
              invoiceDays={props.invoiceDays}
              period={props.period}
              payee={props.payee}
              payer={props.payer}
            />
          }
        />
      </Box>
      <BaseText>Confirm the details before creating the invoice</BaseText>
      <PayeeCard details={props.payee} noEdit />
      <PayerCard details={props.payer} noEdit />
      <Stack sx={{ margin: "10px 2" }}>
        {props.invoiceDays.map((day) => {
          return <DayCard day={day} noDelete />;
        })}
      </Stack>
      <Button color="success" variant="contained" onClick={goToPrintScreen}>
        Confirm
      </Button>
    </div>
  );
}
