import { Check, Clear, Edit } from "@mui/icons-material";
import { Box, ButtonGroup, IconButton, Typography } from "@mui/material";
import React from "react";
import { getTwelveHours, isValidDate } from "../../functions/time";
import { myClasses } from "../../classes/style";
import { myTime } from "../../classes/functions";
function TimeDisplay(props) {
  // let displayText = props.emptyText;
  // if (isValidDate(props.timeObj)) {
  let displayText = myTime.twelveHours(props.time);
  // }
  const openEdit = () => {
    props.updateConfirm(props.keyName, false);
  };
  return (
    <Box sx={{ width: "100%", padding: "12px", ...myClasses.flexRow }}>
      <Typography variant="subtitle1">{displayText}</Typography>
      <IconButton color="primary" onClick={openEdit}>
        <Edit />
      </IconButton>
    </Box>
  );
}

TimeDisplay.defaultProps = {
  emptyText: "No Time Entered",
};

export default TimeDisplay;
