import React from "react";
import { myClasses } from "../classes/style";
import ScreenTitle from "../components/text/ScreenTitle";
import BaseText from "../components/text/BaseText";
import RefreshBtn from "../components/nav/RefreshBtn";
import { Box } from "@mui/system";

import OdoMeterArea from "../components/dataAreas/OdoMeterArea";

function Odomoter(props) {
  return (
    <div>
      <Box sx={{ ...myClasses.flexRow }}>
        <ScreenTitle>Mile Log</ScreenTitle>
        <RefreshBtn
          setPage={props.setPage}
          comp={<Odomoter setPage={props.setPage} />}
        />
      </Box>
      <BaseText>A log for Driving</BaseText>
      <OdoMeterArea />
      <BaseText var="h6">Entries</BaseText>
    </div>
  );
}

export default Odomoter;
