import { ArrowDownward, ArrowUpward } from "@mui/icons-material";
import { Stack } from "@mui/material";
import { useState } from "react";
import lsCache from "../classes/cache";
import InvoiceMaker from "../components/dataAreas/InvoiceMaker";
import Button from "../components/inputs/Button";
import BaseText from "../components/text/BaseText";
import ScreenTitle from "../components/text/ScreenTitle";
import MyHead from "./MyHead";
import InvoiceCard from "../components/displays/InvoiceCard";

export default function InvoiceList(props) {
  document.title = `Invoices - ${MyHead.titleBase}`;
  const [ShowMaker, SetShowMaker] = useState(false);
  let invoiceList = lsCache.rw("invoices");
  if (!invoiceList) {
    invoiceList = [];
  }
  let createNewXML = "";
  if (ShowMaker) {
    createNewXML = <InvoiceMaker setPage={props.setPage} />;
  }

  const openCreate = () => {
    SetShowMaker(!ShowMaker);
  };

  let buttonIcon = <ArrowDownward />;
  if (ShowMaker) {
    buttonIcon = <ArrowUpward />;
  }
  return (
    <div>
      <ScreenTitle>Invoices</ScreenTitle>
      <BaseText>A history of invoices</BaseText>
      <Button onClick={openCreate} variant="contained">
        {buttonIcon} Create New {buttonIcon}
      </Button>
      {createNewXML}
      <Stack>
        <BaseText>Invoice history list</BaseText>
        {invoiceList.map((invoice) => {
          return <InvoiceCard setPage={props.setPage} data={invoice} />;
        })}
      </Stack>
    </div>
  );
}
