import lsCache from "./cache";
import { myDate } from "./functions";

export const validateIn = {
  checkIfDayExists: (date) => {
    // get all history and assemble array of dates
    // foreach and compare
    // return true false
    let ucDays = lsCache.rw("ucDays");
    if (!Array.isArray(ucDays)) {
      return false;
    }
    ucDays.forEach((day) => {
      let sameDate = myDate.sameDate(day.date, date);
      console.log(sameDate);
      if (sameDate) {
        return true;
      }
    });
    return false;
  },
};
