import { Typography } from '@mui/material'
import React from 'react'

function TextButton(props) {
const clicked = () => {
    props.onClick()
}

  return (
    <Typography variant='h6' onClick={clicked} sx={{position : "absolute" , margin : "5% 30px" , top : "90%" , cursor : "pointer" , color : props.color}}>{props.children}</Typography>
  )
}

TextButton.defaultProps = {
  color : "#3333ee"
}

export default TextButton
