import React, { useState } from "react";
import { v4 } from "uuid";
import lsCache from "../../classes/cache";
import { objectFuncs } from "../../classes/functions";
import { payerLabels } from "../../content/listsAndStuff";
import BaseForm from "./index/BaseForm";

function PayerDetails(props) {
  let inputStartOpen = true;
  let preFillData = payerLabels;
  if (props.type === "edit") {
    inputStartOpen = false;
    preFillData = objectFuncs.insertIntoTwoDKey(payerLabels, props.data);
  }

  const confirmChanges = (values) => {
    if (props.type === "edit") {
      lsCache.arrayUpd("payers", values.uuid, values);
    } else {
      values.uuid = v4();
      lsCache.arrayAdd("payers", values);
    }
    props.close();
  };

  const resetChanges = () => {
    props.close();
  };

  return (
    <div>
      <BaseForm
        items={preFillData}
        submit={confirmChanges}
        cancel={resetChanges}
        formName={"payer"}
        storageName="payers"
        startOpen={inputStartOpen}
      />
    </div>
  );
}

export default PayerDetails;
