import React from "react";
import { myDate } from "../../classes/functions";
import InvoicePrinter from "../../pages/InvoicePrinter";
import ClickCard from "./ClickCard";

function InvoiceCard(props) {
  const cardClicked = () => {
    console.log(props);
    props.setPage(
      <InvoicePrinter
        setPage={props.setPage}
        invoiceDays={props.data.invoiceDays}
        period={props.data.period}
        payee={props.data.payee}
        payer={props.data.payer}
      />
    );
  };

  return (
    <ClickCard onClick={cardClicked}>
      {props.data.payee.name} - {myDate.getFullDate(props.data.period.issued)}
    </ClickCard>
  );
}

export default InvoiceCard;
