import { Box } from "@mui/system";
import React, { lazy } from "react";
import lsCache from "../../classes/cache";
import { lazyError } from "../../classes/functions";
import { day } from "../../content/listsAndStuff";

function QuickSignBox(props) {
  try {
    let currentDay = lsCache.rw("currentDay");
    let signInOut = "";
    if (!currentDay) {
      signInOut = "In";
    }
    if (currentDay.timeInConfirm) {
      signInOut = "Out";
    } else {
      signInOut = "confirm";
    }
  } catch {
    let currentDay = day;
    let signInOut = "Catching";
  }
  let signInOut = "Nope";

  return <Box>{signInOut}</Box>;
}

export default QuickSignBox;
