import React, { useState } from "react";
import NavItem from "./NavItem";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import Timesheet from "../../pages/Timesheet";
import History from "../../pages/History";
import HomePage from "../../pages/HomePage";
import { IconButton, Typography } from "@mui/material";
import {
  AccessTime,
  ChevronRight,
  Home,
  FeaturedPlayList,
  DateRange,
  DriveFileMove,
  DirectionsCar,
} from "@mui/icons-material";
import Details from "../../pages/Details";
import InvoiceList from "../../pages/InvoiceList";
import Odomoter from "../../pages/Odomoter";

function NavList({ NavControls }) {
  const [NavExpand, setNavExpand] = React.useState(false);
  const navLinks = [
    {
      title: "Home",
      link: <HomePage setPage={NavControls.setPage} />,
      icon: <Home />,
    },
    {
      title: "Time Sheet",
      link: <Timesheet setPage={NavControls.setPage} />,
      icon: <AccessTime />,
    },
    {
      title: "Mile Log",
      link: <Odomoter setPage={NavControls.setPage} />,
      icon: <DirectionsCar />,
    },
    {
      title: "History",
      link: <History setPage={NavControls.setPage} />,
      icon: <DateRange />,
    },
    {
      title: "Invoice",
      link: <InvoiceList setPage={NavControls.setPage} />,
      icon: <DriveFileMove />,
    },
  ];

  const actionLinks = [
    {
      title: "Details",
      link: <Details setPage={NavControls.setPage} />,
      icon: <FeaturedPlayList />,
    },
    {
      title: "About",
      link: <HomePage setPage={NavControls.setPage} />,
    },
  ];
  const closeDrawer = () => {
    NavControls.toggleDrawer(false);
    setNavExpand(false);
  };

  const expandNow = () => {
    setNavExpand(true);
  };

  if (NavControls.curState) {
    setTimeout(() => {
      if (NavControls.curState) {
        setNavExpand(true);
      }
    }, 1230);
  }

  let navTitle = (
    <IconButton onClick={expandNow}>
      <ChevronRight />
    </IconButton>
  );
  let navClass = "navList-small";

  if (NavExpand) {
    navTitle = (
      <Typography component="h5" variant="h4" align="center">
        Scraapy
      </Typography>
    );
    navClass = "navList-big";
  }

  return (
    <div>
      <Drawer open={NavControls.curState} onClose={closeDrawer}>
        <div className="navTitleBox">{navTitle}</div>
        <List className={navClass}>
          {navLinks.map((link) => {
            return (
              <NavItem
                setPage={NavControls.setPage}
                toggleNav={NavControls.toggleDrawer}
                expand={NavExpand}
                key={link.title}
                title={link.title}
                icon={link.icon}
                link={link.link}
              />
            );
          })}
          <Divider />
          {actionLinks.map((link) => {
            return (
              <NavItem
                setPage={NavControls.setPage}
                toggleNav={NavControls.toggleDrawer}
                expand={NavExpand}
                key={link.title}
                title={link.title}
                icon={link.icon}
                link={link.link}
              />
            );
          })}
        </List>
      </Drawer>
    </div>
  );
}

export default NavList;
