// ? import an open source module that simplifies storing data
import ls from "local-storage";

// ? declare an object that contains custom methods (functions) for storing
const lsCache = {
  // ? Read / Write method
  // ? declare params, set val param to undefined by default
  rw: (key, val = undefined) => {
    if (val !== undefined) {
      // ? if val is suplied, write it
      return ls(key, val);
    } else {
      // ? else read
      return ls(key);
    }
  },
  // ? you can guess
  drop: (key) => {
    ls.remove(key);
  },
  // ? adds a new value to an array
  arrayAdd: (arrName, val) => {
    let curArr = ls(arrName);
    if (curArr === null) {
      curArr = [val];
    } else if (Array.isArray(curArr)) {
      curArr.push(val);
    } else {
      console.log("Value stored not array");
      return false;
    }
    return ls(arrName, curArr);
  },
  // ? deletes a value from an array
  arrayDel: (arrName, id, idKey = "uuid") => {
    let curArr = ls(arrName);
    if (Array.isArray(curArr)) {
      curArr.forEach((item, i) => {
        if (item[idKey] === id) {
          console.log(item[idKey], id);
          curArr.splice(i, 1);
        }
      });
    } else {
      console.log("Value stored not array");
      return false;
    }
    ls(arrName, curArr);
  },
  // ? updates a value in an array
  arrayUpd: (arrName, id, val, idKey = "uuid") => {
    let curArr = ls(arrName);
    if (Array.isArray(curArr)) {
      curArr.forEach((item, i) => {
        if (item[idKey] === id) {
          curArr[i] = val;
        }
      });
    } else {
      console.log("Value stored not array");
      return false;
    }
    ls(arrName, curArr);
  },
};

export default lsCache;
