import { Grid } from '@mui/material'
import React from 'react'
import BaseText from '../text/BaseText'

function InvoiceTextLine(props) {
    console.log(props)
  return (
    <Grid item xs={props.spaces}>
        <BaseText var={props.variant} textSize={props.textSize}>{props.children}</BaseText>
    </Grid>
  )
}

InvoiceTextLine.defaultProps = {
    variant : "subtitle1",
    textSize : "100%"
}

export default InvoiceTextLine