import React, { useState } from "react";
import KmInput from "../inputs/KmInput";
import { Box } from "@mui/material";
import Button from "../inputs/Button";
import { myClasses } from "../../classes/style";
import { odoMeterLog } from "../../content/listsAndStuff";


function OdoMeterArea(props) {


  let initialValue = odoMeterLog;

  // get the most recent out number
  // use as prefill for both thousands feidls and the in singles

  const [OdoValues, SetOdoValues] = useState()

  console.log(OdoValues)

  const inChanged = (e) => {
    console.log(e.target.value)
  };
  const outChanged = (e) => {
    console.log(e.target.value)
  };

  return (
    <Box sx={{ ...myClasses.flexCol, margin: "20px" }}>
      <KmInput handleChange={inChanged}thousands={181} label="Start Reading" />
      <KmInput handleChange={outChanged}thousands={181} label="End Reading" />
      <Button width="50%" margin={18}>
        Submit
      </Button>
    </Box>
  );
}

export default OdoMeterArea;
