// set hours and minutes for a date object
import dateFormat, { masks } from "dateformat";

export const setTimeForDate = (date, hours, minutes) => {
  date.setHours(hours);
  date.setMinutes(minutes);
};
export const roundCurrentTime = () => {
  let currentTime = new Date();
  let currentMinute = currentTime.getMinutes();
  let carryOver = currentMinute % 15;
  currentMinute = currentMinute - carryOver;
  if (carryOver >= 6) {
    carryOver = 15;
  } else {
    carryOver = 0;
  }
  currentTime.setMinutes(currentMinute + carryOver);
  return currentTime;
};

// stack overflow => Borgar
export const isValidDate = (d) => {
  return d instanceof Date && !isNaN(d);
};

// 12 hour time from a date function
export const getTwelveHours = (dateObj) => {
  return dateFormat(dateObj, "hh : MM TT");
};

// 12 hour time from a date function
export const getReadableDate = (date) => {
  date = new Date(date);
  return dateFormat(date, "dddd, dd / mm");
};

export const hoursPastInDay = (timeObj) => {
  let toReturn = timeObj.getHours();
  let minutesAsHours = Math.round((timeObj.getMinutes() / 60) * 100) / 100;
  toReturn += minutesAsHours;
  return toReturn;
};

export const getHoursWorked = (day) => {
  let hoursIn = new Date(day.timeIn);
  let hoursOut = new Date(day.timeOut);
  hoursIn = hoursPastInDay(hoursIn);
  hoursOut = hoursPastInDay(hoursOut);
  let hoursWorked = hoursOut - hoursIn;
  hoursWorked -= day.hadLunch * 0.5;
  hoursWorked -= day.otherTime / 60;
  hoursWorked = Math.round(hoursWorked * 100) / 100;
  return hoursWorked;
};
