import { Typography } from "@mui/material";
import React from "react";

function BaseText(props) {

  return (
    <Typography
      sx={{ margin: "4px 10px 0px", fontSize: props.textSize, ...props.class }}
      component={props.comp}
      variant={props.var}
    >
      {props.children}
    </Typography>
  );
}

BaseText.defaultProps = {
  comp: "p",
  var: "subtitle1",
  class: {},
};

export default BaseText;
