import { IconButton, Typography, Box } from "@mui/material";
import lsCache from "../classes/cache";
import HistoryArea from "../components/lists/HistoryArea";
import BaseText from "../components/text/BaseText";
import ScreenTitle from "../components/text/ScreenTitle";
import MyHead from "./MyHead";
import { myClasses } from "../classes/style";
import RefreshBtn from "../components/nav/RefreshBtn";

export default function History(props) {
  document.title = `History - ${MyHead.titleBase}`;

  return (
    <div>
      <Box sx={{ ...myClasses.flexRow }}>
        <ScreenTitle>History</ScreenTitle>
        <RefreshBtn
          setPage={props.setPage}
          comp={<History setPage={props.setPage} />}
        />
      </Box>
      <BaseText>A list of completed days invoiced or otherwise</BaseText>
      <BaseText var="h6">Open Days</BaseText>
      <HistoryArea days={lsCache.rw("ucDays")} />
    </div>
  );
}
