export const objectMap = (object, thisFunction) => {
  return Object.keys(object).map(thisFunction);
};

// ? returns a new object allowing state refreshes
// ? Can be given arrays of key value pairs
export const objectStateUpdate = (object, key, value) => {
  let newObj = Object.assign({}, object);
  if (Array.isArray(key) && Array.isArray(value)) {
    key.forEach((k , i) => {
      newObj[k] = value[i]
    });
  } else if (!Array.isArray(key ) && !Array.isArray(value)) {
    newObj[key] = value;
  } else {
    console.log("Needs 2 arrays")
    return false;
  }
  return newObj;
};

// ? updates returns a new object allowing state refreshes
// creating fresh copy of object
// update the key
// return new object
export const objectValueUpdate = (object, key, value) => {
  let newObj = Object.assign({}, object);
  console.log(newObj[key]);
  newObj[key].value = value;
  return newObj;
};

export const capitalizeFirstLetter = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};
