import { Typography } from '@mui/material'
import React from 'react'

function DataLabel(props) {
  return (
    <Typography sx={{margin :"4px 10px 0px"}} component={props.comp} variant={props.var}>{props.children}</Typography>
  )
}

DataLabel.defaultProps = {
    comp : "p",
    var : "subtitle1"
}

export default DataLabel