import { Grid, Typography } from "@mui/material";
import React, { useState } from "react";
import MyModal from "./MyModal";
import BaseText from "../text/BaseText";
import NumberInput from "../inputs/index/NumberInput";
import Button from "../inputs/Button";
function MoreTimeOff(props) {
  const [Hours, SetHours] = useState(0);
  const [Minutes, SetMinutes] = useState(0);

  const submit = () => {
    const totalMinutes = parseInt(Hours * 60) + parseInt(Minutes);
    props.submit(totalMinutes);
  };

  const changeMinutes = (e) => {
    SetMinutes(e.target.value);
  };

  const changeHours = (e) => {
    SetHours(e.target.value);
  };

  const closeModal = () => {
    SetMinutes(0);
    SetHours(0);
    props.close()
  }

  return (
    <MyModal isOpen={props.isOpen} close={closeModal}>
      <BaseText var="h6">How long is the extra break?</BaseText>
      <Grid container>
        <Grid item xs={12}>
          <BaseText>Hours</BaseText>
        </Grid>
        <Grid item xs={12}>
          <NumberInput width="100%" onChange={changeHours} />
        </Grid>
        <Grid item xs={12}>
          <BaseText>Minutes</BaseText>
        </Grid>
        <Grid item xs={12}>
          <NumberInput width="100%" onChange={changeMinutes} />
        </Grid>
        <Grid item xs={12}>
          <Button onClick={props.close}>Cancel</Button>
        </Grid>
        <Grid item xs={12}>
          <Button onClick={submit}>Enter</Button>
        </Grid>
      </Grid>
    </MyModal>
  );
}

export default MoreTimeOff;
