import React, { useState } from "react";
import { TextField, IconButton, ButtonGroup, Typography } from "@mui/material";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { roundCurrentTime } from "../../functions/time";
import { Check, Clear } from "@mui/icons-material";
import { MobileDatePicker } from "@mui/x-date-pickers/MobileDatePicker";
import DateDisplay from "./DateDisplay";

export default function DatePicker(props) {

  let startValue = props.data.defaultValue
if (props.value) {
  startValue = props.value
}
startValue = new Date(startValue)


let initialDate = new Date(startValue);
// console.log(initialDate) 


const [Value, SetValue] = useState(initialDate);

// console.log(Value)
  const handleChange = (newValue) => {
    SetValue(newValue);
    let valueStamp = Value.getTime();
    props.updateDate(props.keyName, valueStamp);

  };

  let iconXML = "";
  if (props.buttons) {
    iconXML = (
      <div>
        <IconButton color="success" onClick={submitChange}>
          <Check />
        </IconButton>
        <IconButton color="error" onClick={clearChange}>
          <Clear />
        </IconButton>
      </div>
    );
  }

  const submitChange = () => {
    let valueStamp = Value.getTime();
    props.updateDate(props.keyName, valueStamp);
    props.confirmKey(props.keyName);
  };

  const clearChange = () => {
    props.updateDate(props.keyName, props.data.defaultValue);
    SetValue(props.initialDate);
  };

  let swappingXML = (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
    <MobileDatePicker
      label={props.label}
      inputFormat="dd/MM/yyyy"
      value={Value}
      onChange={handleChange}
      onAccept={submitChange}
      renderInput={(params) => <TextField {...params} />}
    />
    {iconXML}
  </LocalizationProvider>
  );
  if (props.confirmed) {
    swappingXML = (
      <DateDisplay
        updateConfirm={props.confirmKey}
        keyName={props.keyName}
        date={Value}
      />
    );
  }


  return (
    <div>
     {swappingXML}
    </div>
  );
}

DatePicker.defualtProps = {
  hours: 6,
  minutes: 30,
  label: "Time",
  button: true,
};
