import React from "react";
import DayCard from "../displays/DayCard";
import { Stack, Typography } from "@mui/material";
const HistoryArea = (props) => {
  let log = "";
  let theXML = "";
  const theseDays = props.days;
  if (Array.isArray(theseDays)) {
    theXML = (
      <Stack sx={{ borderTop: "1px solid lightgrey", marginY: "10px" }}>
        {theseDays.map((day, i) => {
          return <DayCard day={day} key={i} />;
        })}
      </Stack>
    );
  } else {
    theXML = <Typography>No Data</Typography>;
  }

  return <div>{theXML}</div>;
};

export default HistoryArea;
