import React from "react";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import NavList from "./NavList";
import { Box } from "@mui/system";

function NavBar(props) {
  const [NavListState, setNavListState] = React.useState(false);

  const NavControls = {
    curState: NavListState,
    toggleDrawer: (newVal = undefined) => {
      if (newVal !== undefined) {
        setNavListState(newVal);
      } else {
        setNavListState(!NavListState);
      }
    },
    setPage: (page) => {
      props.setPage(page);
    },
  };

  const iconClicked = () => {
    NavControls.toggleDrawer();
  };

  return (
    <nav>
      <Box sx={{ height: "64px", borderBottom: "1px solid #ddd" }}>
        <IconButton
          size="large"
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{
            mr: 2,
            position: "fixed",
            top: "10px",
            left: "10px",
            background: "#fafaffdd",
            "&:hover": { background: "#fff" },
          }}
          onClick={iconClicked}
        >
          <MenuIcon />
        </IconButton>
      </Box>
      <NavList NavControls={NavControls} />
    </nav>
  );
}

export default NavBar;
