import { Grid, Box, Divider } from "@mui/material";
import React from "react";
import { myDate, payMath } from "../classes/functions";
import InvoiceLine from "../components/displays/InvoiceLine";
import InvoiceTextLine from "../components/displays/InvoiceTextLine";
import TextButton from "../components/inputs/TextButton";
import BaseText from "../components/text/BaseText";
import ScreenTitle from "../components/text/ScreenTitle";
import InvoiceList from "../pages/InvoiceList.js";

function InvoicePrinter(props) {
  document.title = `${props.payee.name} - ${myDate.getFullDate()}`;
  document.querySelector("nav").style.display = "none";

  const goBack = () => {
    document.querySelector("nav").style.display = "block";
    props.setPage(<InvoiceList setPage={props.setPage} />);
  };

  const headingStyles = {
    borderRight: "1px solid lightgrey",
    borderBottom: "5px solid lightGrey",
  };
  const totalHours = payMath.getTotalHours(props.invoiceDays);
  const payRateWithCents = payMath.centsOnDollars(props.payer.payRate);

  return (
    <Box
      sx={{
        minWidth: "1080px",
        maxWidth: "1080px",
        overflow: "hidden",
        marginTop: "60px",
      }}
    >
      <TextButton color="lightgrey" onClick={goBack}>
        2EZ - Data Solutions
      </TextButton>
      <Grid
        container
        direction="row"
        justifyContent="flex-start"
        alignItems="flex-end"
        spacing={2}
        sx={{ minWidth: "1080px" }}
      >
        <InvoiceTextLine spaces={2} variant="h2">
          <ScreenTitle>Invoice</ScreenTitle>
        </InvoiceTextLine>
        <InvoiceTextLine spaces={2} textSize="130%">
          Time Period
        </InvoiceTextLine>
        <Grid item xs={6}>
          <BaseText textSize="130%">
            {myDate.getFullDate(props.period.start)} - {"  "}
            {myDate.getFullDate(props.period.end)}
          </BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>Pay To</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>BSB: {props.payee.bsb}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>Account No. : {props.payee.accountNo}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>{props.payee.name}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>ABN: {props.payee.abn}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>Address: {props.payee.address}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>Mobile: {props.payee.phoneNo}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>Email: {props.payee.email}</BaseText>
        </Grid>
        <Grid item xs={12}>
          <Divider />
        </Grid>
        <Grid item xs={4}>
          <BaseText>Payee: {props.payer.name}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText> Work Done: {props.payer.workDone}</BaseText>
        </Grid>
        <Grid item xs={4}>
          <BaseText>Issued: {myDate.getFullDate(props.period.issued)}</BaseText>
        </Grid>
        <Grid item xs={12}>
          <Divider />
        </Grid>
        <Grid item ex={12} container sx={{ textAlign: "center" }}>
          <Grid item xs={3} sx={headingStyles}>
            <BaseText var="h6">Date</BaseText>
          </Grid>
          <Grid item xs={3} sx={headingStyles}>
            <BaseText var="h6">Address</BaseText>
          </Grid>
          <Grid item xs={2} sx={headingStyles}>
            <BaseText var="h6">Start</BaseText>
          </Grid>
          <Grid item xs={2} sx={headingStyles}>
            <BaseText var="h6">End</BaseText>
          </Grid>
          <Grid item xs={1} sx={headingStyles}>
            <BaseText var="h6">Breaks</BaseText>
          </Grid>
          <Grid item xs={1} sx={headingStyles}>
            <BaseText var="h6">Hours</BaseText>
          </Grid>
        </Grid>
        <Grid item xs={12} sx={{ padding: "0px" }}>
          {props.invoiceDays.map((day) => {
            return <InvoiceLine dayData={day} />;
          })}
        </Grid>
        <Grid items xs={4}></Grid>
        <Grid items xs={2}>
          <BaseText var="h5">Hourly Rate</BaseText>
        </Grid>
        <Grid items xs={2} sx={{ textAlign: "right" }}>
          <BaseText var="h5">${payRateWithCents}</BaseText>
        </Grid>
        <Grid items xs={2}>
          <BaseText var="h5">Total Hours</BaseText>
        </Grid>
        <Grid items xs={2} sx={{ textAlign: "right" }}>
          <BaseText var="h5">{totalHours}</BaseText>
        </Grid>
        <Grid items xs={8}></Grid>
        <Grid items xs={2}>
          <BaseText var="h5">Total Amount</BaseText>
        </Grid>
        <Grid items xs={2} sx={{ textAlign: "right" }}>
          <BaseText var="h5">${totalHours * props.payer.payRate}</BaseText>
        </Grid>
      </Grid>
    </Box>
  );
}

export default InvoicePrinter;
