import {
  Box,
  ButtonGroup,
  Divider,
  IconButton,
  TextField,
  Typography,
} from "@mui/material";
import { Check, Clear, WindowOutlined } from "@mui/icons-material";
import React, { useState } from "react";
import { capitalizeFirstLetter } from "../../functions";

function LabelToInput(props) {
  let value = props.value;
  if (!value) {
    value = props.defaultValue;
  }

  const [InputOpen, SetInputOpen] = useState(props.startOpen);
  const [InputValue, SetInputValue] = useState(value);

  const openInput = () => {
    SetInputOpen(true);
    const thisInput = document.getElementById(`text-field-${props.label}`);
  };

  const inputChanged = (e) => {
    SetInputValue(e.target.value);
  };

  const keyPressed = (e) => {
    if (e.key === "Enter" || e.keyCode === 13) {
      submitChange();
    }
  };

  const submitChange = () => {
    // get the value from the input
    props.submit(props.keyName, InputValue);
    SetInputOpen(false);
  };

  const clearChange = () => {
    SetInputValue(value);
    SetInputOpen(false);
  };

  // ? allows for setting of diiferent input types
  let inputType = "text";
  if (props.type) {
    inputType = props.type;
  }
  // ? the text half of input or text

  const label = (
    <Box>
      {" "}
      <Typography>{props.label}</Typography>
      <Typography variant="subtitle1" onClick={openInput}>
        {value}
      </Typography>
    </Box>
  );

  const input = (
    <Box sx={{ width: "100%", display: "flex", flexDirection: "row" }}>
      <TextField
        autoFocus
        id={`text-field-${props.label}`}
        onChange={inputChanged}
        onKeyPress={keyPressed}
        onBlur={submitChange}
        sx={{
          flexBasis: "2",
          MinWidth: "280px",
          width: "75%",
        }}
        variant="filled"
        type={inputType}
        label={props.label}
        defaultValue={value}
      />
      <ButtonGroup variant="filled" sx={{ margin: "10px", maxWidth: "30%" }}>
        <IconButton color="error" onClick={clearChange}>
          <Clear />
        </IconButton>
      </ButtonGroup>
    </Box>
  );

  let inputOrText = label;

  // ? the input half
  if (InputOpen) {
    inputOrText = input;
  }

  return (
    <div>
      <Box
        sx={{
          padding: "8px",
          marginY: "3px",
          backgroundColor: "#eee",
          "&:hover": {
            opacity: [0.9, 0.8, 0.7],
          },
        }}
      >
        {inputOrText}
      </Box>
      <Divider />
    </div>
  );
}

LabelToInput.defaultProps = {
  type: false,
  startOpen: false,
};

export default LabelToInput;
