import * as React from "react";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import { arrayFuncs } from "../../classes/functions";

export default function OptionsSlect(props) {
  const [SelectedItem, SetSelectedItem] = React.useState("");

  const handleChange = (event) => {
    console.log("changes");
    SetSelectedItem(event.target.value);
    props.onChange(props.keyName, event.target.value);
  };

  // console.log(props.data);

  return (
    <Box sx={{ minWidth: 120, margin: "10px 0" }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">{props.label}</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={SelectedItem}
          label={props.label}
          onChange={handleChange}
        >
          {arrayFuncs.nsMap(props.data, (payee) => {
            return (
              <MenuItem value={payee[props.itemValueKey]}>
                {payee[props.itemLabelKey]}
              </MenuItem>
            );
          })}
        </Select>
      </FormControl>
    </Box>
  );
}
