import React from "react";
import { Card as MuiCard, CardActions, CardContent } from "@mui/material";
const Card = (props) => {
  const cardClicked = () => {
    props.onClick();
  };

  let actionsXML = "";
  if (props.onClick && props.actionArea) {
    actionsXML = (
      <CardActions onClick={cardClicked}>{props.actionArea} </CardActions>
    );
  }

  return (
    <MuiCard
      sx={{
        width: "100%",
      }}
    >
      <CardContent
        sx={{
          ...props.class,
        }}
      >
        {props.children}
      </CardContent>
      {actionsXML}
      {props.iconButtons}
    </MuiCard>
  );
};

Card.defaultProps = {
  class: { m: 1, p: 1 },
  iconButtons: "",
};

export default Card;
