import MyHead from "./MyHead.js";
import BaseText from "../components/text/BaseText";
import ScreenTitle from "../components/text/ScreenTitle";
import PayeeList from "../components/lists/PayeeList.js";
import PayerList from "../components/lists/PayerList.js";
import RefreshBtn from "../components/nav/RefreshBtn.js";
import { Box } from "@mui/system";
import { myClasses } from "../classes/style.js";
import AddressList from "../components/lists/AddressList.js";

export default function Details(props) {
  document.title = `Details - ${MyHead.titleBase}`;
  return (
    <div>
      <Box sx={{ ...myClasses.flexRow }}>
        <ScreenTitle>Details</ScreenTitle>
        <RefreshBtn
          setPage={props.setPage}
          comp={<Details setPage={props.setPage} />}
        />
      </Box>
      <BaseText>Save the details used for creating invoices.</BaseText>
      <PayeeList />
      <PayerList />
      <AddressList />
    </div>
  );
}
