import { Divider, Grid } from '@mui/material'
import React from 'react'
import { myDate, myTime } from '../../classes/functions'
import BaseText from '../text/BaseText'

function InvoiceLine(props) {
    const dateOf = myDate.getFullDate(props.dayData.date)
    const address = props.dayData.location
    const start = myTime.twelveHours(props.dayData.timeIn)
    const end = myTime.twelveHours(props.dayData.timeOut)
    const breaks =( props.dayData.hadLunch * 30 + props.dayData.otherTime) / 60
    const hoursWorked = myTime.getHoursWorked(props.dayData)

    
  return (
    <Grid container sx={{textAlign : "center" , padding: "0px"}}>
        <Grid item xs={3}  sx={{borderRight : "1px solid lightgrey"}}>
            <BaseText>{dateOf}</BaseText>
        </Grid>
        <Grid item xs={3}  sx={{borderRight : "1px solid lightgrey"}}>
            <BaseText>{address}</BaseText>
        </Grid>
        <Grid item xs={2}  sx={{borderRight : "1px solid lightgrey"}}>
            <BaseText>{start}</BaseText>
        </Grid>
        <Grid item xs={2}  sx={{borderRight : "1px solid lightgrey"}}>
            <BaseText>{end}</BaseText>
        </Grid>
        <Grid item xs={1}  sx={{borderRight : "1px solid lightgrey"}}>
            <BaseText>{breaks}</BaseText>
        </Grid>
        <Grid item xs={1}>
            <BaseText>{hoursWorked}</BaseText>
        </Grid>
        <Grid item xs={12}>
            <Divider />
        </Grid>
    </Grid>
  )
}

export default InvoiceLine