import { Add } from "@mui/icons-material";
import { Container, IconButton, Stack } from "@mui/material";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { myClasses } from "../../classes/style";
import AddressCard from "../displays/AddressCard";
import AddressForm from "../forms/AddressForm";
import BaseText from "../text/BaseText";

function AddressList() {
  const [ListData, SetListData] = useState([]);
  const [FormOpen, SetFormOpen] = useState(false);
  let formXML = "";

  const newPayee = () => {
    SetFormOpen(true);
  };

  const submitForm = () => {
    SetListData({ ...ListData });
  };

  const closeForm = () => {
    SetFormOpen(false);
  };

  if (FormOpen) {
    formXML = <AddressForm close={closeForm} />;
  }

  let listXML = "Nono Found";
  const storedAddresses = lsCache.rw("addresses");
  if (Array.isArray(storedAddresses)) {
    listXML = (
      <Stack>
        {storedAddresses.map((payee) => {
          return <AddressCard details={payee} />;
        })}
      </Stack>
    );
  }

  return (
    <div>
      <Container sx={{ ...myClasses.flexRow }}>
        <BaseText var="h6">Address Details</BaseText>
        <IconButton onClick={newPayee}>
          <Add />
        </IconButton>
      </Container>
      {listXML}
      {formXML}
    </div>
  );
}

export default AddressList;
