import React, { useState } from "react";
import { TextField, IconButton, ButtonGroup, Typography } from "@mui/material";
import { TimePicker as MuiTimePicker } from "@mui/x-date-pickers/TimePicker";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { isValidDate, roundCurrentTime } from "../../functions/time";
import { Check, Clear } from "@mui/icons-material";
import { myClasses } from "../../classes/style";
import { Box } from "@mui/system";
import TimeDisplay from "./TimeDisplay";
import DataLabel from "../text/DataLabel";

export default function TimePicker(props) {
  // console.log(props);
  let initialTime = roundCurrentTime();
  if (props.value) {
    initialTime = props.value;
  }
  initialTime = new Date(initialTime);
  const [Value, SetValue] = useState(initialTime);

  const handleChange = (newValue) => {
    SetValue(newValue);
    let valueStamp = Value.getTime();
    props.updateTime(props.keyName, valueStamp);
  };

  const submitChange = () => {
    let valueStamp = Value.getTime();
    props.updateTime(props.keyName, valueStamp);
    props.confirmKey(props.keyName);
  };

  const clearChange = () => {};

  let swappingXML = (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <MuiTimePicker
        value={Value}
        onChange={handleChange}
        onAccept={submitChange}
        renderInput={(params) => <TextField {...params} />}
      />
      <ButtonGroup variant="filled" sx={{ maxWidth: "30%" }}>
        <IconButton color="success" onClick={submitChange}>
          <Check />
        </IconButton>
        <IconButton color="error" onClick={clearChange}>
          <Clear />
        </IconButton>
      </ButtonGroup>
    </LocalizationProvider>
  );
  if (props.confirmed) {
    swappingXML = (
      <TimeDisplay
        updateConfirm={props.confirmKey}
        keyName={props.keyName}
        time={Value}
      />
    );
  }

  return (
    <div>
      <DataLabel>{props.data.label}</DataLabel>
      <Box sx={{ ...myClasses.flexRow,  ...myClasses.flexSpace ,  padding: "6px 10px" }}>
        {swappingXML}
      </Box>
    </div>
  );
}

TimePicker.defualtProps = {
  hours: 6,
  minutes: 30,
  label: "Time",
};
