import { Add } from "@mui/icons-material";
import { Container, IconButton, Stack } from "@mui/material";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { myClasses } from "../../classes/style";
import PayerCard from "../displays/PayerCard";
import PayerForm from "../forms/PayerForm";
import BaseText from "../text/BaseText";

function PayerList() {
  const [ListData, SetListData] = useState([]);
  const [FormOpen, SetFormOpen] = useState(false);
  let formXML = "";

  const newPayee = () => {
    SetFormOpen(true);
  };

  const submitForm = () => {
    SetListData({ ...ListData });
  };

  const closeForm = () => {
    SetFormOpen(false);
  };

  if (FormOpen) {
    formXML = <PayerForm close={closeForm} />;
  }

  let listXML = "Nono Found";
  const storedPayees = lsCache.rw("payers");
  if (Array.isArray(storedPayees)) {
    listXML = (
      <Stack>
        {storedPayees.map((payee) => {
          return <PayerCard details={payee} />;
        })}
      </Stack>
    );
  }

  return (
    <div>
      <Container sx={{ ...myClasses.flexRow }}>
        <BaseText var="h6">Payer Details</BaseText>
        <IconButton onClick={newPayee}>
          <Add />
        </IconButton>
      </Container>
      {listXML}
      {formXML}
    </div>
  );
}

export default PayerList;
