import React from "react";
import { Card as MuiCard, CardActionArea, CardActions } from "@mui/material";
const ClickCard = (props) => {
  const cardClicked = () => {
    props.onClick();
  };

  return (
    <MuiCard
      sx={{
        width: "100%",
      }}
    >
      <CardActions
        onClick={cardClicked}
        sx={{
          ...props.class,
        }}
      >
        {props.children}
      </CardActions>
      {props.iconButtons}
    </MuiCard>
  );
};

ClickCard.defaultProps = {
  class: { m: 1, p: 1 },
  iconButtons: "",
};

export default ClickCard;
