import dateFormat from "dateformat";

// ? Does a basic error check
export const lazyError = (
  thisFunction,
  params,
  handle = () => {
    console.log("BAAAAAAddd Params Homie");
    return null;
  }
) => {
  try {
    thisFunction(params);
  } catch {
    handle();
  }
};

export const myTime = {
  twelveHours: (dateObj) => {
    // console.log(dateObj)
    dateObj = new Date(dateObj);
    return dateFormat(dateObj, "hh : MM TT");
  },
  removeTimeOfDay: (date, setToEnd = false) => {
    date = new Date(date);
    if (setToEnd) {
      date.setHours(23);
      date.setMinutes(59);
      date.setSeconds(59);
    } else {
      date.setHours(0);
      date.setMinutes(0);
      date.setSeconds(0);
    }
    return date.getTime();
  },
  hoursPastInDay: (time) => {
    time = new Date(time);
    let toReturn = time.getHours();
    let minutesAsHours = Math.round((time.getMinutes() / 60) * 100) / 100;
    toReturn += minutesAsHours;
    return toReturn;
  },
  getHoursWorked: (day) => {
    let hoursIn = new Date(day.timeIn);
    let hoursOut = new Date(day.timeOut);
    hoursIn = myTime.hoursPastInDay(hoursIn);
    hoursOut = myTime.hoursPastInDay(hoursOut);
    let hoursWorked = hoursOut - hoursIn;
    hoursWorked -= day.hadLunch * 0.5;
    hoursWorked -= day.otherTime / 60;
    hoursWorked = Math.round(hoursWorked * 100) / 100;
    return hoursWorked;
  },
};

export const myDate = {
  sameDate: (date1, date2) => {
    console.log(date1);
    try {
      date1 = new Date(date1);
      date2 = new Date(date2);
      if (date1.getDay() === date2.getDay()) {
        return true;
      }
      return false;
    } catch {
      return null;
    }
  },

  dateBetween: (date, start, end) => {
    date = new Date(date).getTime();
    start = myTime.removeTimeOfDay(start);
    end = myTime.removeTimeOfDay(end, true);
    return date >= start && date <= end;
  },

  getFullDate: (date) => {
    return dateFormat(date, "dd / mm / yyyy");
  },

  dateFromStamp: (stamp) => {
    return new Date(stamp);
  },
};

export const objectFuncs = {
  // ? Foreach through Object keys like an array
  forEach: (obj, thisFunction) => {
    try {
      Object.keys(obj).forEach(thisFunction);
    } catch {
      console.log("BAAAAAAddd Params Homie");
      return null;
    }
  },

  // ? maps through Object keys like an array
  map: (obj, thisFunction) => {
    try {
      return Object.keys(obj).map(thisFunction);
    } catch {
      console.log("BAAAAAAddd Params Homie");
      return null;
    }
  },

  // ? Extracts key from 2nd D of 2D obj, returns 1D obj
  extractKeyFromTwoD: (object, key) => {
    try {
      let toReturn = {};
      objectFuncs.forEach(object, (mapKey) => {
        if (object[mapKey][key] !== undefined) {
          toReturn[mapKey] = object[mapKey][key];
        }
      });
      return toReturn;
    } catch {
      console.log("Probably bad obj / Arr");
      return null;
    }
  },

  // ? updates a key with a new value, returns upd obj
  updateValue: (obj, key, value) => {
    try {
      obj[key] = value;
      return { ...obj };
    } catch {
      console.log("Probably bad obj / Arr");
      return null;
    }
  },

  // ? inserts an object inside another under a set key
  insertIntoTwoDKey: (twoDobj, oneDobj, inKey = "value") => {
    objectFuncs.forEach(oneDobj, (key) => {
      if (oneDobj[key] !== undefined && oneDobj[key] !== null) {
        twoDobj[key][inKey] = oneDobj[key];
      }
    });
    return { ...twoDobj };
  },

  getFromArrayWithKV: (objArr, val, key = "uuid") => {
    try {
      return objArr.map((obj) => {
        if ((obj[key] = val)) {
          return obj;
        }
        return false;
      })[0];
    } catch {
      console.log("Probably bad obj / Arr");
      return null;
    }
  },

  updateInTwoD: (object, value, outKey, inKey = "value") => {
    let newObj = object;
    newObj[outKey][inKey] = value;
    return { ...newObj };
  },
};

export const arrayFuncs = {
  // ? catches null values, and arrayifys them
  nullSafe: (arr) => {
    if (!arr) {
      arr = [];
    }
    return arr;
  },
  nsForEach: (
    arr,
    func = (i) => {
      console.log(i);
    }
  ) => {
    arr = arrayFuncs.nullSafe(arr);
    arr.forEach((item) => {
      func(item);
    });
  },
  nsMap: (
    arr,
    func = (i) => {
      console.log(i);
    }
  ) => {
    arr = arrayFuncs.nullSafe(arr);
    return arr.map((item) => {
      return func(item);
    });
  },
};

export const payMath = {
  getTotalHours: (daysArray) => {
    let total = 0;
    daysArray.forEach((day) => {
      total += myTime.getHoursWorked(day);
    });
    return total;
  },
  centsOnDollars: (rate) => {
    let cents = Math.round((rate % 1) * 100);
    let showExtraCent = "0";
    if (cents > 10) {
      showExtraCent = "";
    }
    let toReturn = `${rate - (rate % 1)}.${cents + showExtraCent}`;
    return toReturn;
  },
};

export const milesMAth = {
  odoDifference: (start, end) => {
    return end - start;
  },
};
