import { Add } from "@mui/icons-material";
import { Container, IconButton, Stack } from "@mui/material";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { myClasses } from "../../classes/style";
import PayeeCard from "../displays/PayeeCard";
import PayeeForm from "../forms/PayeeForm";
import Button from "../inputs/Button";
import BaseText from "../text/BaseText";
function PayeeList() {
  const [ListData, SetListData] = useState([]);
  const [FormOpen, SetFormOpen] = useState(false);
  let formXML = "";

  const newPayee = () => {
    SetFormOpen(true);
  };

  const submitForm = () => {
    SetListData({ ...ListData });
  };

  const closeForm = () => {
    SetFormOpen(false);
  };

  if (FormOpen) {
    formXML = <PayeeForm close={closeForm} />;
  }

  let listXML = "Nono Found";
  const storedPayees = lsCache.rw("payees");
  if (Array.isArray(storedPayees)) {
    listXML = (
      <Stack>
        {storedPayees.map((payee) => {
          return <PayeeCard details={payee} />;
        })}
      </Stack>
    );
  }

  return (
    <div>
      <Container sx={{ ...myClasses.flexRow }}>
        <BaseText var="h6">Payee Details</BaseText>
        <IconButton onClick={newPayee}>
          <Add />
        </IconButton>
      </Container>
      {listXML}
      {formXML}
    </div>
  );
}

export default PayeeList;
