import { Checkbox, Grid, Stack, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { objectStateUpdate } from "../../functions";
import { day } from "../../content/listsAndStuff";
import TimeDisplay from "../inputs/TimeDisplay";
import TimePicker from "../inputs/TimePicker";
import Button from "../inputs/Button.js";
import DaySuimmary from "../displays/DaySummary";
import DatePicker from "../inputs/DatePicker";
import DateDisplay from "../inputs/DateDisplay";
import { myClasses, myColors } from "../../classes/style";
import { validateIn } from "../../classes/validation";
import DataLabel from "../text/DataLabel";
import { v4 as uuid } from "uuid";
import { objectFuncs } from "../../classes/functions";
import MoreTimeOff from "../modals/MoreTimeOff.js";
import LocationSelect from "../inputs/LocationSelect";

let initialValues = objectFuncs.extractKeyFromTwoD(day, "defaultValue");
const cachedDay = lsCache.rw("currentDay");
if (cachedDay) {
  initialValues = cachedDay;
}

let initialConfirms = objectFuncs.extractKeyFromTwoD(day, "confirmed");
const cachedConfirms = lsCache.rw("currentDayConfirms");
if (cachedConfirms) {
  initialConfirms = cachedConfirms;
}

// ! Check both of the day archives to see if this day is currently filed.
function TimeSheetData() {
  const [DayValues, SetDayValues] = useState(initialValues);
  const [ConfirmedValues, SetConfirmedValues] = useState(initialConfirms);
  const [ModalOpen, SetModalOpen] = useState(false);

  const openExtraTime = () => {
    SetModalOpen(true);
  };

  const closeModal = () => {
    SetModalOpen(false);
  };

  const submitExtraTime = (val) => {
    console.log(val);
    updateKey("otherTime", val);
    closeModal();
  };

  // console.log(DayValues);
  const updateKey = (key, value) => {
    //! Think of a better way of doing this
    let newObj = objectFuncs.updateValue(DayValues, key, value);
    SetDayValues(newObj);
    lsCache.rw("currentDay", newObj);
  };

  const updateConfirm = (key, value = true) => {
    let newObj = objectFuncs.updateValue(ConfirmedValues, key, value);
    SetConfirmedValues(newObj);
    lsCache.rw("currentDayConfirms", newObj);
  };

  const confirmChanges = () => {
    // finish and send detauks to the unclaimed days pile

    // ! alert the user that day exists on file
    // ! or for any other issues ... Negative hours ect

    let dayData = DayValues;
    dayData.uuid = uuid();
    lsCache.arrayAdd("ucDays", dayData);
    lsCache.drop("currentDay");
    lsCache.drop("currentDayConfirms");
    SetDayValues(objectFuncs.extractKeyFromTwoD(day, "defaultValue"));
    SetConfirmedValues(objectFuncs.extractKeyFromTwoD(day, "confirmed"));
  };

  const storedAddresses = lsCache.rw("addresses");

  const lunchClicked = (e) => {
    updateKey("hadLunch", !DayValues.hadLunch);
  };

  let daySummary = "";
  if (
    ConfirmedValues.date &&
    ConfirmedValues.timeIn &&
    ConfirmedValues.timeOut
    // DayValues.timeIn.confirmed &&
    // DayValues.timeOut.confirmed &&
    // DayValues.date.confirmed
  ) {
    daySummary = (
      <DaySuimmary
        day={DayValues}
        button={
          <Button
            color="success"
            onClick={confirmChanges}
            margin={10}
            width="240px"
          >
            Another Dollar
          </Button>
        }
      />
    );
  }
  return (
    <div>
      <MoreTimeOff
        isOpen={ModalOpen}
        close={closeModal}
        submit={submitExtraTime}
      />
      <Stack>
        <DatePicker
          data={day.date}
          updateDate={updateKey}
          confirmKey={updateConfirm}
          value={DayValues.date}
          keyName="date"
          confirmed={ConfirmedValues.date}
        />
        <TimePicker
          data={day.timeIn}
          updateTime={updateKey}
          confirmKey={updateConfirm}
          value={DayValues.timeIn}
          keyName="timeIn"
          confirmed={ConfirmedValues.timeIn}
        />
        <TimePicker
          data={day.timeOut}
          updateTime={updateKey}
          confirmKey={updateConfirm}
          value={DayValues.timeOut}
          keyName="timeOut"
          confirmed={ConfirmedValues.timeOut}
        />
        <LocationSelect
          data={storedAddresses}
          itemValueKey="address"
          itemLabelKey="name"
          onChange={updateKey}
          // confirmKey={updateConfirm}
          keyName="location"
          label="Location"
        />
        <Box
          sx={{
            ...myClasses.flexRow,
            ...myClasses.flexSpace,
            padding: "15px 8px",
          }}
        >
          <Typography
            sx={{
              ...myClasses.flexRow,
              padding: "10px",
            }}
          >
            Had Lunch
          </Typography>
          <Checkbox
            color="secondary"
            checked={DayValues.hadLunch}
            onChange={lunchClicked}
          />
        </Box>
        <Box
          sx={{
            ...myClasses.flexRow,
            ...myClasses.flexSpace,
            padding: "15px 8px",
          }}
        >
          <Typography
            sx={{
              ...myClasses.flexRow,
              padding: "10px",
              color: myColors.info,
            }}
            onClick={openExtraTime}
          >
            Any other breaks
          </Typography>
          <Typography
            sx={{
              ...myClasses.flexRow,
              padding: "10px",
              color: myColors.info,
            }}
            onClick={openExtraTime}
          >
            {DayValues.otherTime} Minutes
          </Typography>
        </Box>
        {daySummary}
      </Stack>
    </div>
  );
}

export default TimeSheetData;
