import { ConfirmationNumber, Delete } from "@mui/icons-material";
import { Box, IconButton, Stack, Typography } from "@mui/material";
import React, { useState } from "react";
import lsCache from "../../classes/cache";
import { myTime } from "../../classes/functions";
import { myClasses } from "../../classes/style";
import { objectMap } from "../../functions";
import { getHoursWorked, getReadableDate } from "../../functions/time";
import Button from "../inputs/Button";

import ClickCard from "./ClickCard";

const DayCard = (props) => {
  const [Expanded, SetExpanded] = useState(false);
  const [ConfimDelete, SetConfimDelete] = useState(false);
  const [Deleted, SetDeleted] = useState(false);
  const hoursWorked = getHoursWorked(props.day);
  const dateOf = getReadableDate(props.day.date);

  const cardClicked = () => {
    SetConfimDelete(false);
    SetExpanded(!Expanded);
  };

  const delClicked = (e) => {
    if (!ConfimDelete) {
      SetConfimDelete(true);
    } else {
      // delete from localstorage
      lsCache.arrayDel("ucDays", props.day.uuid);
      SetDeleted(true);
    }
  };

  let expandedData = "";
  let iconButtons = "";
  if (Expanded) {
    expandedData = (
      <Stack>
        {objectMap(props.day, (key) => {
          if (key === "date") {
            return (
              <Typography key={key}>{`${key} : ${getReadableDate(
                props.day[key]
              )}`}</Typography>
            );
          } else if (key === "timeIn" || key === "timeOut") {
            return (
              <Typography key={key}>{`${key} : ${myTime.twelveHours(
                props.day[key]
              )}`}</Typography>
            );
          } else if (key === "uuid") {
          } else {
            return (
              <Typography key={key}>{`${key} : ${props.day[key]}`}</Typography>
            );
          }
        })}
      </Stack>
    );
    if (ConfimDelete && !props.noDelete) {
      iconButtons = (
        <Box sx={{ ...myClasses.flexCol }}>
          <Button
            paddingX="10px"
            paddingY="10px"
            color="error"
            onClick={delClicked}
          >
            Are you Sure?
          </Button>
        </Box>
      );
    } else if (!props.noDelete) {
      iconButtons = (
        <Box sx={{ ...myClasses.flexCol }}>
          <Button
            color="error"
            paddingX="5px"
            paddingY="5px"
            width="auto"
            onClick={delClicked}
          >
            <Delete />
          </Button>
        </Box>
      );
    }
  }


  
  let deleteXML = "";
  if (Deleted) {
    deleteXML = (
      <Typography
        sx={{
          p: 1,
          background: "#daa",
          color: "white",
          fontWeight: "700",
          textAlign: "center",
        }}
      >
        Deleted
      </Typography>
    );
  }

  return (
    <div>
      {deleteXML}

      <ClickCard onClick={cardClicked} iconButtons={iconButtons}>
        <Box>
          <Typography>{dateOf}</Typography>
          <Typography>{hoursWorked}</Typography>
        </Box>
        <Box>{expandedData}</Box>
      </ClickCard>
    </div>
  );
};

export default DayCard;
