import React, { useState } from "react";
import DatePicker from "../inputs/DatePicker";
import DataLabel from "../text/DataLabel";
import { Divider, Stack } from "@mui/material";
import BaseText from "../text/BaseText";
import { objectStateUpdate } from "../../functions";
import { invoiceTimeFrame } from "../../content/listsAndStuff";
import { getReadableDate } from "../../functions/time";
import Button from "../inputs/Button";
import lsCache from "../../classes/cache";
import { myDate, objectFuncs } from "../../classes/functions";
import OptionSelect from "../inputs/OptionsSelect";
import InvoiceConfirm from "../../pages/InvoiceConfirm";
import { day } from "../../content/listsAndStuff";

function InvoiceMaker(props) {
  const [InvoicePeriod, SetInvoiePeriod] = useState(invoiceTimeFrame);
  const [InvoicePayee, SetInvoicePayee] = useState(null);
  const [InvoicePayer, SetInvoicePayer] = useState(null);

  const payeesList = lsCache.rw("payees");
  const payersList = lsCache.rw("payers");

  const updateKey = (key, value) => {
    const newObj = objectStateUpdate(InvoicePeriod, key, value);
    SetInvoiePeriod(newObj);
  };

  const updatePayee = (payee) => {
    SetInvoicePayee(payee);
  };

  const updatePayer = (payer) => {
    SetInvoicePayer(payer);
  };

  const generateInvoice = () => {
    const ucDays = lsCache.rw("ucDays");
    let invoiceDays = [];
    ucDays.forEach((day) => {
      if (
        myDate.dateBetween(day.date, InvoicePeriod.start, InvoicePeriod.end)
      ) {
        invoiceDays.push(day);
      }
    });

    let payeeData = objectFuncs.getFromArrayWithKV(payeesList, InvoicePayee);
    let payerData = objectFuncs.getFromArrayWithKV(payersList, InvoicePayer);
    // if (payeeData && payerData) {
    props.setPage(
      <InvoiceConfirm
        setPage={props.setPage}
        invoiceDays={invoiceDays}
        period={InvoicePeriod}
        payee={payeeData}
        payer={payerData}
      />
    );
    // }
  };

  return (
    <Stack
      sx={{ border: "1px solid blue", margin: "8px", padding: "12px 4px" }}
    >
      <BaseText>Enter the timeframe for the invoice</BaseText>
      <DataLabel>Start Date: {getReadableDate(InvoicePeriod.start)}</DataLabel>
      <DatePicker
        data={day.date}
        updateDate={updateKey}
        keyName="start"
        updateConfirm=""
        value={InvoicePeriod.start}
      />
      <DataLabel>End Date: {getReadableDate(InvoicePeriod.end)}</DataLabel>
      <DatePicker
        data={day.date}
        updateDate={updateKey}
        keyName="end"
        value={InvoicePeriod.end}
      />
      <DataLabel>
        Date of Issue: {getReadableDate(InvoicePeriod.issued)}
      </DataLabel>
      <DatePicker
        data={day.date}
        updateDate={updateKey}
        keyName="issued"
        value={InvoicePeriod.issued}
      />
      <Divider sx={{ margin: "18px" }} />
      <OptionSelect
        data={payeesList}
        label="Payee"
        onChange={updatePayee}
        itemLabelKey="nickName"
        itemValueKey="uuid"
      />
      <OptionSelect
        data={payersList}
        label="Payer"
        onChange={updatePayer}
        itemLabelKey="nickName"
        itemValueKey="uuid"
      />
      <Button onClick={generateInvoice} color="success">
        Generate
      </Button>
    </Stack>
  );
}

export default InvoiceMaker;
