import { Stack } from "@mui/material";
import React, { useState } from "react";
import LabelToInput from "../../inputs/LabelToInput";
import { myClasses } from "../../../classes/style";
import { Box } from "@mui/system";
import Button from "../../inputs/Button";
import { objectFuncs } from "../../../classes/functions";
import { v4 } from "uuid";

function BaseForm(props) {
  const [FormValues, SetFormValues] = useState(
    objectFuncs.extractKeyFromTwoD(props.items, "value")
  );
  const updateValue = (key, value) => {
    SetFormValues(objectFuncs.updateValue(FormValues, key, value));
  };

  const submitForm = () => {
    props.submit(FormValues);
  };

  return (
    <div>
      <Stack>
        {
          // ! this part is hella confusing, its a custom function that is imported
          // ? the object map, loops through the keys of an object running a function for each of them
          // ? look up js array.map if you dont know about maps, kinda like a foreach that returns
        }
        {objectFuncs.map(props.items, (key) => {
          if (key !== "uuid") {
            return (
              <LabelToInput
                value={FormValues[key]}
                label={props.items[key].label}
                defaultValue={props.items[key].defaultValue}
                keyName={key}
                submit={updateValue}
                startOpen={props.startOpen}
              />
            );
          }
        })}
      </Stack>
      {
        // ? this kinda shows the value of the MUI compoents
      }
      <Box sx={{ ...myClasses.flexRow, padding: "15px 8px" }}>
        <Button variant="contained" color="success" onClick={submitForm}>
          Save
        </Button>
        <Button color="error" onClick={props.cancel}>
          Clear
        </Button>
      </Box>
    </div>
  );
}

export default BaseForm;
