import { Edit, Clear } from "@mui/icons-material";
import { Container, IconButton } from "@mui/material";
import React, { useState } from "react";
import { myClasses } from "../../classes/style";
import BaseText from "../text/BaseText";
import Card from "./Card";
import PayerForm from "../forms/PayerForm";
import Button from "../inputs/Button";
function PayerCard(props) {
  const [IsEditing, SetIsEditing] = useState(false);
  const editDetails = () => {
    SetIsEditing(!IsEditing);
  };
  let buttonsXML = "";

  let editXML = "";
  let delXML = "";
  if (IsEditing) {
    editXML = (
      <PayerForm type="edit" data={props.details} close={editDetails} />
    );
    delXML = (
      <Button color="error" onClick={editDetails}>
        Delete
      </Button>
    );
  }
  if (!props.noEdit) {
    buttonsXML = (
      <IconButton onClick={editDetails}>
        {delXML}
        <Edit />
      </IconButton>
    );
  }

  return (
    <div>
      <Card class={{ ...myClasses.flexRow }}>
        <BaseText>{props.details.nickName}</BaseText>
        {buttonsXML}
      </Card>
      {editXML}
    </div>
  );
}

export default PayerCard;
