// ? creates an object from the exported module in the reference file

import React, { useState } from "react";
import { v4 } from "uuid";
// ? lsCache is also commented
import lsCache from "../../classes/cache";
import { objectFuncs } from "../../classes/functions";
import { addressLabels } from "../../content/listsAndStuff";
import BaseForm from "./index/BaseForm";

function AddressForm(props) {
  let inputStartOpen = true;
  let preFillData = addressLabels;
  if (props.type === "edit") {
    inputStartOpen = false;
    preFillData = objectFuncs.insertIntoTwoDKey(addressLabels, props.data);
  }

  const confirmChanges = (values) => {
    if (props.type === "edit") {
      lsCache.arrayUpd("addresses", values.uuid, values);
    } else {
      values.uuid = v4();
      lsCache.arrayAdd("addresses", values);
    }
    props.close();
  };

  const resetChanges = () => {
    props.close();
    // // set details from storage
    // let cached = lsCache.rw("details");
    // // ? if found set, else use dummy
    // if (cached) {
    //   SetDetails(cached);
    // } else {
    //   SetDetails(addressLabels);
    // }
  };

  console.log(preFillData);
  return (
    <div>
      <BaseForm
        items={preFillData}
        submit={confirmChanges}
        cancel={resetChanges}
        formName={"addresses"}
        storageName="addresses"
        startOpen={inputStartOpen}
      />
    </div>
  );
}

// ? this exports the component function as the default export in this file
// ? allows it to be imported and used by other components
export default AddressForm;
