import { Check, Clear, Edit } from "@mui/icons-material";
import { Box, ButtonGroup, IconButton, Typography } from "@mui/material";
import React from "react";
import { getReadableDate, isValidDate } from "../../functions/time";
import { myClasses } from "../../classes/style";
import { myDate } from "../../classes/functions";

const DateDisplay = (props) => {
  // let displayText = props.emptyText;
  // if (isValidDate(props.timeObj)) {
  let displayText = myDate.getFullDate(props.date)
  // }

  const openEdit = () => {
    props.updateConfirm(props.keyName, false, "Confirmed");
  };

  return (
    <Box sx={{ padding: "12px", ...myClasses.flexRow }}>
      <Typography variant="subtitle1">{displayText}</Typography>
      <IconButton color="primary" onClick={openEdit}>
        <Edit />
      </IconButton>
    </Box>
  );
};

DateDisplay.defaultProps = {
  emptyText: "No Time Entered",
};

export default DateDisplay;
