import { TextField } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { myClasses } from "../../classes/style";
import NumberInput from "./index/NumberInput";

function KmInput(props) {


  return (
    <Box sx={{...myClasses.flexRow}}>
      <NumberInput label="Thousand Km" onChange={props.handleChange} />
      <NumberInput label="Kms" onChange={props.handleChange} />
    </Box>
  );
}

export default KmInput;
